import React from 'react'

function Clock() {
    const styles = {
        fontSize: 25
    }
    const date = new Date()
    const hours = date.getHours()
    let timeOfDay
    if (hours < 12) {
        timeOfDay = "Morning"
        styles.color = "#FF4500"
    } else if (hours > 12 && hours < 17) {
        timeOfDay = "Afternoon"
        styles.color = "#FFD700"
    } else {
        timeOfDay = "Night"
        styles.color = "#9400D3"
    }
    return (
        <div>
            <h1 style={styles}> Good {timeOfDay} </h1>
        </div>
    )
}

export default Clock